import { Component } from '@angular/core';
import { map } from 'rxjs/operators';
import { Breakpoints, BreakpointObserver } from '@angular/cdk/layout';
import { ActivatedRoute } from '@angular/router';
import { MatDialog } from '@angular/material/dialog';
import { QuestionsComponent } from 'src/app/forms/questions/questions.component';
import { FormBuilder, Validators, FormArray } from '@angular/forms';

@Component({
  selector: 'app-paper',
  templateUrl: './paper.component.html',
  styleUrls: ['./paper.component.css']
})
export class PaperComponent {
  /** Based on the screen size, switch from standard to one column per row */


  questions:any[]=[];
edit:boolean=false;
question:string;
option1:string;
option2:string
option3:string;
option4:string;
answer:string;
forms;
  constructor(private router:ActivatedRoute,private dialog:MatDialog,private fb:FormBuilder) {
    this.forms = fb.array([
      
    ])
  }
  formEdit(index){
    this.questions[index].edit=true;
  }
 addforms(value){
  let form = this.fb.group({
    'title':[value.title,[Validators.required]],
    'options':this.fb.array([
      this.fb.control(value.options[0],[Validators.required]),
      this.fb.control(value.options[1],[Validators.required]),
      this.fb.control(value.options[2],[Validators.required]),
      this.fb.control(value.options[3],[Validators.required]),
    ]),
    "answer":[value.answer,[Validators.required]],
  })
   this.getforms.push(form);
 }

  get getforms(){
    return this.forms as FormArray;
  }


  save(){
    this.questions.push({question:this.question,edit:false,option1:this.option1,option2:this.option2,option3:this.option3,option4:this.option4,answer:this.answer});
  }
  createQuestion(){
    this.openDialog(QuestionsComponent)
    .subscribe(data=>{
      if(data)
      console.log(data)
      this.questions.push(data)
    })
  }
  openDialog(component){
    return this.dialog.open(component)
    .afterClosed()
  }
}
