import { Inject, Injectable } from '@angular/core';
import { Device,Transport, Producer, Consumer, DataProducer, DataConsumer, RtpCapabilities } from 'mediasoup-client/lib/types';
import { SocketService } from './socket.service';
import * as MediasoupClient from 'mediasoup-client';
import { Subject } from 'rxjs';

const PC_PROPRIETARY_CONSTRAINTS =
{
	optional : [ { googDscp: true } ]
};

@Injectable({
  providedIn: 'root'
})
export class MediasoupService extends SocketService {
  mediasoupDevice:Device;
  transport:  Map<string, Transport> = new Map();
  producer:  Map<string, Producer> = new Map();
  consumer:  Map<string, Consumer> = new Map();
  dataProducer:  Map<string, DataProducer> = new Map();
  dataConsumer:  Map<string, DataConsumer> = new Map();
  localStreams:  Map<string, MediaStream> = new Map();
  remoteStreams:  Map<string, MediaStream> = new Map();
  peers:any;
  constructor(@Inject('token') token) {
    super(token);
   }
 
   async promoteAllLobbyPeers()
   {
     console.debug('promoteAllLobbyPeers()');
 
 
     try
     {
       await this.sendRequest({ method: 'promoteAllPeers' });
     }
     catch (error)
     {
       console.error('promoteAllLobbyPeers() [error:"%o"]', error);
     }

   }
 
   async promoteLobbyPeer(peerId)
   {
     console.debug('promoteLobbyPeer() [peerId:"%s"]', peerId);
 
 
     try
     {
       await this.sendRequest({ method: 'promotePeer', data: { peerId } });
     }
     catch (error)
     {
       console.error('promoteLobbyPeer() [error:"%o"]', error);
     }
 
   }
 
   async modifyPeerConsumer(peerId, type, mute)
   {
     console.debug(
       'modifyPeerConsumer() [peerId:"%s", type:"%s"]',
       peerId,
       type
     );
 
     try
     {
       for (const consumer of this.consumer.values())
       {
         if (consumer.appData.peerId === peerId && consumer.appData.source === type)
         {
           if (mute)
             await this._pauseConsumer(consumer);
           else
             await this._resumeConsumer(consumer);
         }
       }
     }
     catch (error)
     {
       console.error('modifyPeerConsumer() [error:"%o"]', error);
     }
   }
 
   async _pauseConsumer(consumer)
   {
     console.debug('_pauseConsumer() [consumer:"%o"]', consumer);
 
     if (consumer.paused || consumer.closed)
       return;
 
     try
     {
       await this.sendRequest({ method: 'pauseConsumer', data: { consumerId: consumer.id } });
 
       consumer.pause();
     }
     catch (error)
     {
       console.error('_pauseConsumer() [error:"%o"]', error);
     }
   }
 
   async _resumeConsumer(consumer)
   {
     console.debug('_resumeConsumer() [consumer:"%o"]', consumer);
 
     if (!consumer.paused || consumer.closed)
       return;
 
     try
     {
       await this.sendRequest({ method: 'resumeConsumer', data: { consumerId: consumer.id } });
 
       consumer.resume();
     }
     catch (error)
     {
       console.error('_resumeConsumer() [error:"%o"]', error);
     }
   }
 
   async setMaxSendingSpatialLayer(spatialLayer)
   {
     console.debug('setMaxSendingSpatialLayer() [spatialLayer:"%s"]', spatialLayer);
 
     try
     {
       if (this.producer.get('video'))
         await this.producer.get('video').setMaxSpatialLayer(spatialLayer);
       if (this.producer.get('screen'))
         await this.producer.get('screen').setMaxSpatialLayer(spatialLayer);
     }
     catch (error)
     {
       console.error('setMaxSendingSpatialLayer() [error:"%o"]', error);
     }
   }
 
   async setConsumerPreferredLayers(consumerId, spatialLayer, temporalLayer)
   {
     console.debug(
       'setConsumerPreferredLayers() [consumerId:"%s", spatialLayer:"%s", temporalLayer:"%s"]',
       consumerId, spatialLayer, temporalLayer);
 
     try
     {
       await this.sendRequest(
         { method: 'setConsumerPreferedLayers', data: { consumerId, spatialLayer, temporalLayer } });
 
     }
     catch (error)
     {
       console.error('setConsumerPreferredLayers() [error:"%o"]', error);
     }
   }
 
   async setConsumerPriority(consumerId, priority)
   {
     console.debug(
       'setConsumerPriority() [consumerId:"%s", priority:%d]',
       consumerId, priority);
 
     try
     {
       await this.sendRequest({ method: 'setConsumerPriority', data: { consumerId, priority } });
 
     }
     catch (error)
     {
       console.error('setConsumerPriority() [error:"%o"]', error);
     }
   }
 
   async requestConsumerKeyFrame(consumerId)
   {
     console.debug('requestConsumerKeyFrame() [consumerId:"%s"]', consumerId);
 
     try
     {
       await this.sendRequest({ method: 'requestConsumerKeyFrame', data: { consumerId } });
     }
     catch (error)
     {
       console.error('requestConsumerKeyFrame() [error:"%o"]', error);
     }
   }

    async loadDevice(){
      this.mediasoupDevice = new MediasoupClient.Device();
      const routerRtpCapabilities:RtpCapabilities =
        await this.sendRequest({ method: 'getRouterRtpCapabilities' });
      routerRtpCapabilities.headerExtensions = routerRtpCapabilities.headerExtensions
        .filter((ext) => ext.uri !== 'urn:3gpp:video-orientation');

      await this.mediasoupDevice.load({ routerRtpCapabilities });
    }

    async webrtcTransport(forceTcp=false,producing=false,consuming=false){
      return await this.sendRequest(
        {
        method: 'createWebRtcTransport', 
        data: {
          forceTcp,
          producing,
          consuming
        }
        });
    }
    async sendTransport(forceTcp=false,producing=false,consuming=false){
      const {id,iceParameters,iceCandidates,dtlsParameters,sctpParameters}:any= await this.webrtcTransport(forceTcp,producing,consuming);
      const sendTransport = this.mediasoupDevice.createSendTransport(
        {
          id,
          iceParameters,
          iceCandidates,
          dtlsParameters,
          sctpParameters,
          proprietaryConstraints : PC_PROPRIETARY_CONSTRAINTS
        });
        this.transport.set('send',sendTransport);
        this.transport.get('send').on(
          'connect', ({ dtlsParameters }, callback, errback) => // eslint-disable-line no-shadow
          {
            this.sendRequest(
              {
              method: 'connectWebRtcTransport', data: {
                transportId: this.transport.get('send').id,
                dtlsParameters
              }
              })
              .then(callback)
              .catch(errback);
          });

        this.transport.get('send').on(
          'produce', async ({ kind, rtpParameters, appData }, callback, errback) =>
          {
            try
            {
              // eslint-disable-next-line no-shadow
              const { id }:any = await this.sendRequest(
                {
                  method: 'produce', data: {
                                             transportId: this.transport.get('send').id,
                                             kind,
                                             rtpParameters,
                                             appData
                }
                });

              callback({ id });
            }
            catch (error)
            {
              errback(error);
            }
          });

          this.transport.get('send').on(
            'producedata', async ({label, sctpStreamParameters,protocol, appData }, callback, errback) =>
            {
              try
              {
                // eslint-disable-next-line no-shadow
                const { id }:any = await this.sendRequest(
                  {
                    method: 'producedata', data: {
                                                transportId:this.transport.get('send').id,
                                               label,
                                               sctpStreamParameters,
                                               protocol,
                                               appData
                  }
                  });
  console.log("dataproducerId ",id)
                callback({ id });
              }
              catch (error)
              {
                errback(error);
              }
            });

            this.transport.get('send').on('connectionstatechange',(state)=>{
              switch (state) {
                case 'connecting':
                    console.log('publishing...');
                    break;
    
                case 'connected':
                    console.log('published');
                    break;
    
                case 'failed':
                    console.log('failed');
                    this.transport.get('send').close();
                    break;
    
                default:
                    break;
            }
            });
    }
    async recvTransport(forceTcp=false,producing=false,consuming=false){
      const {id,iceParameters,iceCandidates,dtlsParameters,sctpParameters}:any=await this.webrtcTransport(forceTcp,producing,consuming);
      const recvTransport = this.mediasoupDevice.createRecvTransport(
        {
          id,
          iceParameters,
          iceCandidates,
          dtlsParameters,
          sctpParameters,
        });

        this.transport.set('receive',recvTransport);
        this.transport.get('receive').on(
          'connect', ({ dtlsParameters }, callback, errback) => // eslint-disable-line no-shadow
          {
            this.sendRequest(
              {
              method: 'connectWebRtcTransport', data: {
                transportId: this.transport.get('receive').id,
                dtlsParameters
              }
              })
              .then(callback)
              .catch(errback);
          });

          this.transport.get('receive').on('connectionstatechange',(state)=>{
            switch (state) {
              case 'connecting':
                  console.log('publishing...');
                  break;
  
              case 'connected':
                  console.log('published');
                  break;
  
              case 'failed':
                  console.log('failed');
                  this.transport.get('send').close();
                  break;
  
              default:
                  break;
          }
          });
    }

    async joinRequest(displayName){
      return await this.sendRequest(
        {
        method: 'join', data: {
          displayName: displayName,
          rtpCapabilities: this.mediasoupDevice.rtpCapabilities
        }
        });
    }

    async createDataProducer(label){
    const dataProducer =  await this.transport.get('send').produceData({
 label});
 console.log("dataproducer readystate ",dataProducer.readyState);
    this.dataProducer.set('message',dataProducer);
    dataProducer.on('transportclose',()=>{
      console.log('data Tansport close');
    });
    dataProducer.on('error',(error)=>{
      console.log("dataproducer error ",error)
    })
    dataProducer.on('close',()=>{
      console.log("datproducer close")
    })
    console.log("dataproducer readystate ",dataProducer.readyState)
    }
 
}
