import { Component, AfterViewInit } from '@angular/core';
import { map } from 'rxjs/operators';
import { Breakpoints, BreakpointObserver } from '@angular/cdk/layout';
import { ProfileService } from './profile.service';
import { FormBuilder, Validators } from '@angular/forms';
import { MyErrorStateMatcher } from 'src/app/forms/errormatcher';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})
export class ProfileComponent implements AfterViewInit {
  /** Based on the screen size, switch from standard to one column per row */
  cards = this.breakpointObserver.observe(Breakpoints.Handset).pipe(
    map(({ matches }) => {
      if (matches) {
        return [
          { title: 'Card 1', cols: 1, rows: 1 },
          { title: 'Card 2', cols: 1, rows: 1 },
          { title: 'Card 3', cols: 1, rows: 1 },
          { title: 'Card 4', cols: 1, rows: 1 }
        ];
      }

      return [
        { title: 'Card 1', cols: 2, rows: 1 },
        { title: 'Card 2', cols: 1, rows: 1 },
        { title: 'Card 3', cols: 1, rows: 2 },
        { title: 'Card 4', cols: 1, rows: 1 }
      ];
    })
  );
  profile:any;
  form;
  matcher = new MyErrorStateMatcher();
  constructor(private breakpointObserver: BreakpointObserver,private service:ProfileService,private fb:FormBuilder) {
    this.form = fb.group({
      'first_name':['',[Validators.required]],
      'last_name':['',[Validators.required]],
      "email":['',[Validators.required,Validators.email]],
      "username":['',Validators.required],
    })
    
  }
ngAfterViewInit(){
    this.service.get('profile')
    .subscribe((response:any)=>{
      delete response.user['_id'];
      this.form.setValue(response.user);
      this.form.disable();
    })
}

  formEdit(){
    this.form.enable();
  }
  save(value){
    this.form.disable();
    this.service.update('profile',value)
    .subscribe(response=>{
      console.log(response);
    })
  }
  property(value){
    return this.form.get(value)
  }
}
